import React, { Component } from 'react';
import { TabContent, TabPane, Nav, NavItem, NavLink, Row, Col } from 'reactstrap';
import classnames from 'classnames';
import './perfil-nav.scss';

class PerfilNav extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: '1',
    };
    this.onToggle = this.onToggle.bind(this);
  }

  onToggle(tab) {
    const { activeTab } = this.state;
    if (activeTab !== tab) {
      this.setState({ activeTab: tab });
    }
  }

  render() {
    const { activeTab } = this.state;
    return (
      <nav className="perfil_nav_cont">
        <ul className="nav_cont">
          <li>
            <button className="button_">historia</button>
          </li>
          <li>
            <button className="button_">informacion</button>
          </li>
          <li>
            <button className="button_">amigos</button>
          </li>
          <li>
            <button className="button_">deseos</button>
          </li>
        </ul>
      </nav>
    );
  }
};

export default PerfilNav;
